body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.game {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.inline {
  display: inline-block;
}

.table {
  display: table;
}

.center {
  position: relative;
  top:250px;
  margin: auto;
}

.center-horizontal {
  position: relative;
  margin: auto;
}

.draw-box {
  width: 465px;
  height: 450px;
}

.char-icon {
  width: 116px;
  height: 146px;
}

.single-draw {
  background-image: url("./resource/single_draw.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 340px;
  height: 170px;
}

.eleven-draw {
  background-image: url("./resource/eleven_draw.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 340px;
  height: 170px;
}

.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}

.example-appear {
  opacity: 0.01;
}

.example-appear.example-appear-active {
  opacity: 1;
  -webkit-transition: opacity .5s ease-in;
  -o-transition: opacity .5s ease-in;
  transition: opacity .5s ease-in;
}

.whole-screen {
  width: 680px;
  height: 975px;
}

